.Searchbar {
    top: 0;
    left: 0;
    max-width: 400px;
    position: sticky;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 64px;
    padding-right: 24px;
    padding-left: 24px;
    padding-top: 12px;
    padding-bottom: 12px;
    color: #fff;
    
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
        0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}
.container {
    
    max-width: 1200px;
    padding: 0 20px;
    margin: 0 auto;
    
}

.SearchForm {
    display: flex;
    align-items: center;
    width: 400px;
    max-width: 600px;
    background-color: #fff;
    border-radius: 3px;
    overflow: hidden;
    border: 1px solid blue;
    margin: 0 auto;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
            0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.SearchForm_button {
    position: relative;
    display: inline-block;
    width: 48px;
    height: 48px;
    border: 0;
    background-size: 40%;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
}

.SearchForm_button:hover {
    opacity: 1;
}

.SearchForm_button_label {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 50%;
    height: 50%;
    transform: translate(-50%, -50%);
    padding: 0;
    fill: black;
}

.SearchForm_input {
    display: inline-block;
    width: 100%;
    font: inherit;
    font-size: 20px;
    border: none;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;
    
}

.SearchForm_input::placeholder {
    font: inherit;
    font-size: 18px;
}